
#clip_button{right:10%;background:#1b81cb
}
#cancel_clip,#clip_button{position:absolute;bottom:5.333vw;width:21.333vw;height:10.667vw;border:none;border-radius:.533vw;color:#fff
}
#cancel_clip{left:10%;background:#5abe70
}
#clip_container.container{z-index:2;position:fixed;padding-top:16vw;left:0;top:0;right:0;bottom:0;background:#000
}
#clip_container.container>div{position:absolute;width:100%;height:100%;top:50%;left:50%;-webkit-transform:translate(-50%,-50%);transform:translate(-50%,-50%)
}
#clip_image{max-width:100%
}
.cropper-container{font-size:0;line-height:0;position:relative;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;direction:ltr;-ms-touch-action:none;touch-action:none
}
.crop_loading,.crop_success{display:none;position:fixed;top:0;left:0;width:100%;height:100%;z-index:1
}
.crop_loading .crop_content{position:absolute;top:50%;left:50%;text-align:center;background:#000;opacity:.9;height:17.6vw;width:37.333vw;vertical-align:middle;color:#fff;padding-top:5.333vw;font-size:4.267vw;border-radius:.8vw;-webkit-transform:translate(-50%,-50%);transform:translate(-50%,-50%)
}
.crop_loading .crop_content img{margin-top:4vw;margin-bottom:2.667vw
}
.crop_success .crop_success_text{position:absolute;top:50%;left:50%;text-align:center;background:#000;opacity:.9;width:32vw;height:8vw;color:#fff;line-height:8vw;font-size:4.267vw;border-radius:.8vw;-webkit-transform:translate(-50%,-50%);transform:translate(-50%,-50%)
}
.cropper-container img{display:block;min-width:0!important;max-width:none!important;min-height:0!important;max-height:none!important;width:100%;height:100%;image-orientation:0deg
}
.cropper-canvas,.cropper-crop-box,.cropper-drag-box,.cropper-modal,.cropper-wrap-box{position:absolute;top:0;right:0;bottom:0;left:0
}
.cropper-wrap-box{overflow:hidden
}
.cropper-drag-box{opacity:0;background-color:#fff
}
.cropper-modal{opacity:.5;background-color:#000
}
.cropper-view-box{display:block;overflow:hidden;width:100%;height:100%;outline:1px solid #39f;outline-color:rgba(51,153,255,.75)
}
.cropper-dashed{position:absolute;display:block;opacity:.5;border:0 dashed #eee
}
.cropper-dashed.dashed-h{top:33.33333%;left:0;width:100%;height:33.33333%;border-top-width:1px;border-bottom-width:1px
}
.cropper-dashed.dashed-v{top:0;left:33.33333%;width:33.33333%;height:100%;border-right-width:1px;border-left-width:1px
}
.cropper-center{position:absolute;top:50%;left:50%;display:block;width:0;height:0;opacity:.75
}
.cropper-center:after,.cropper-center:before{position:absolute;display:block;content:" ";background-color:#eee
}
.cropper-center:before{top:0;left:-.8vw;width:1.867vw;height:1px
}
.cropper-center:after{top:-.8vw;left:0;width:1px;height:1.867vw
}
.cropper-face,.cropper-line,.cropper-point{position:absolute;display:block;width:100%;height:100%;opacity:.1
}
.cropper-face{top:0;left:0;background-color:#fff
}
.cropper-line{background-color:#39f
}
.cropper-line.line-e{top:0;right:-.8vw;width:1.333vw;cursor:e-resize
}
.cropper-line.line-n{top:-.8vw;left:0;height:1.333vw;cursor:n-resize
}
.cropper-line.line-w{top:0;left:-.8vw;width:1.333vw;cursor:w-resize
}
.cropper-line.line-s{bottom:-.8vw;left:0;height:1.333vw;cursor:s-resize
}
.cropper-point{width:1.333vw;height:1.333vw;opacity:.75;background-color:#39f
}
.cropper-point.point-e{top:50%;right:-.8vw;margin-top:-.8vw;cursor:e-resize
}
.cropper-point.point-n{top:-.8vw;left:50%;margin-left:-.8vw;cursor:n-resize
}
.cropper-point.point-w{top:50%;left:-.8vw;margin-top:-.8vw;cursor:w-resize
}
.cropper-point.point-s{bottom:-.8vw;left:50%;margin-left:-.8vw;cursor:s-resize
}
.cropper-point.point-ne{top:-.8vw;right:-.8vw;cursor:ne-resize
}
.cropper-point.point-nw{top:-.8vw;left:-.8vw;cursor:nw-resize
}
.cropper-point.point-sw{bottom:-.8vw;left:-.8vw;cursor:sw-resize
}
.cropper-point.point-se{right:-.8vw;bottom:-.8vw;width:5.333vw;height:5.333vw;cursor:se-resize;opacity:1
}
@media (min-width:768px){
.cropper-point.point-se{width:15px;height:15px
}
}
@media (min-width:992px){
.cropper-point.point-se{width:10px;height:10px
}
}
@media (min-width:1200px){
.cropper-point.point-se{width:5px;height:5px;opacity:.75
}
}
.cropper-point.point-se:before{position:absolute;right:-50%;bottom:-50%;display:block;width:200%;height:200%;content:" ";opacity:0;background-color:#39f
}
.cropper-invisible{opacity:0
}
.cropper-bg{background-image:url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABAAAAAQAQMAAAAlPW0iAAAAA3NCSVQICAjb4U/gAAAABlBMVEXMzMz////TjRV2AAAACXBIWXMAAArrAAAK6wGCiw1aAAAAHHRFWHRTb2Z0d2FyZQBBZG9iZSBGaXJld29ya3MgQ1M26LyyjAAAABFJREFUCJlj+M/AgBVhF/0PAH6/D/HkDxOGAAAAAElFTkSuQmCC")
}
.cropper-hide{position:absolute;display:block;width:0;height:0
}
.cropper-hidden{display:none!important
}
.cropper-move{cursor:move
}
.cropper-crop{cursor:crosshair
}
.cropper-disabled .cropper-drag-box,.cropper-disabled .cropper-face,.cropper-disabled .cropper-line,.cropper-disabled .cropper-point{cursor:not-allowed
}


