
.m-chat-emoji{position:absolute;top:-51.2vw;top:-12rem;height:51.2vw;height:12rem;left:0;width:100%;border-top:1px solid #ccc;background-color:#fff
}
.m-chat-emoji .emoji-channel{position:relative;width:100%;height:auto;margin:0 1rem
}
.m-chat-emoji .emoji-channel .emoji-album{display:inline-block;padding:.1rem;width:7.68vw;width:1.8rem;height:7.68vw;height:1.8rem;border-right:1px solid #f0f0f0
}
.m-chat-emoji .emoji-channel .emoji-album img{margin:0;display:block;width:inherit;height:inherit
}
.m-chat-emoji .emoji-channel .emoji-album.active{background-color:#f0f0f0
}
.m-chat-emoji .emoji-content{position:relative;width:100%;height:42.667vw;height:10rem;background-color:#f0f0f0;overflow-y:auto
}
.m-chat-emoji .emoji-content .cnt{position:relative;display:block;margin:.4rem auto;text-align:left
}
.m-chat-emoji .emoji-content .emoji-item{display:inline-block;width:7.467vw;height:7.467vw;padding:.533vw;vertical-align:middle
}
.m-chat-emoji .emoji-content .emoji-item img{width:inherit;height:inherit
}
.m-chat-emoji .emoji-content .pinup-item{width:11.733vw;height:11.733vw
}
@media screen and (min-width:300px) and (max-width:420px){
.m-chat-emoji .emoji-content .cnt{width:300px
}
}
@media screen and (min-width:420px) and (max-width:600px){
.m-chat-emoji .emoji-content .cnt{width:420px
}
}
@media screen and (min-width:600px) and (max-width:720px){
.m-chat-emoji .emoji-content .cnt{width:600px
}
}
@media screen and (min-width:720px) and (max-width:1080px){
.m-chat-emoji .emoji-content .cnt{width:720px
}
}
@media screen and (min-width:1080px){
.m-chat-emoji .emoji-content .cnt{width:1080px
}
}


