
.u-icon-img img{position:relative;margin:0;padding:0;width:100%;height:100%
}
.m-album,.m-article,.m-main,.m-room{position:absolute;padding-top:3.6rem;display:block;-webkit-box-sizing:border-box;box-sizing:border-box;top:0;bottom:0;left:0;width:100%;height:100%;-webkit-overflow-scrolling:touch;overflow-y:auto;overflow-x:hidden
}
.m-article{top:0;background:#fffbf9
}
.m-tab{position:absolute;top:0;left:0;margin:0;padding:2.667vw 0;width:100%;height:15.36vw;height:3.6rem;overflow:hidden;z-index:1
}
.m-tab .vux-header-left,.m-tab .vux-header-right{top:5.333vw
}
.m-tab .m-tab-top{position:relative;padding:0;font-size:5.547vw;font-size:1.3rem
}
.m-tab .m-tab-top.vux-button-group{margin:1.067vw auto
}
.m-tab .m-tab-right{position:absolute;right:0;top:.427vw;top:.1rem;font-size:3.413vw;font-size:.8rem;text-align:right;width:17.067vw;width:4rem
}
.m-article-main,.m-chat-main{position:relative;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column;-webkit-box-sizing:border-box;box-sizing:border-box;width:100%;height:100%;overflow:hidden
}
.g-window .vux-header .m-tab-top{width:80%;margin:0 10%;overflow:hidden;text-overflow:ellipsis;white-space:nowrap
}
.m-chat-main{padding:0 0 4rem
}
.m-chat-main.m-chat-history{padding-bottom:0
}
.m-chat-main .m-chat-list{position:relative;display:block;-webkit-box-sizing:border-box;box-sizing:border-box;padding:1rem 2%;width:100%;-webkit-overflow-scrolling:touch;overflow-y:scroll;overflow-x:hidden
}
.m-chat-editor{position:absolute;bottom:0;left:0;width:100%;height:17.067vw;height:4rem;background-color:#e5f4ff
}
.m-chat-editor-main{display:block;padding:0;height:100%
}
.m-chat-editor-main,.m-chat-editor-main .u-editor-input{position:relative;-webkit-box-sizing:border-box;box-sizing:border-box;width:100%
}
.m-chat-editor-main .u-editor-input{display:inline-block;margin:0;padding:.2rem 10rem .2rem .2rem;height:17.067vw;height:4rem
}
.m-chat-editor-main .u-editor-input textarea{position:relative;display:inline-block;-webkit-box-sizing:border-box;box-sizing:border-box;padding:.2rem;font-size:4.267vw;font-size:1rem;width:100%;height:100%;text-align:left;border:1px solid #ccc;border-radius:.4rem
}
.m-chat-editor-main .u-editor-input i{font-style:normal
}
.m-chat-editor-main .u-editor-input .u-btn-record{position:relative;display:inline-block;-webkit-box-sizing:border-box;box-sizing:border-box;margin:1rem 0 0;width:100%;height:8.107vw;height:1.9rem;line-height:7.68vw;line-height:1.8rem;border:1px solid #ccc;border-radius:.3rem;text-align:center;font-size:4.267vw;font-size:1rem
}
.m-chat-editor-main .u-editor-input .u-btn-record.recording{background:#ccc
}
.m-chat-editor-main .u-editor-input .u-record-time{position:absolute;top:-10.667vw;top:-2.5rem;left:0;width:17.92vw;width:4.2rem;height:9.387vw;height:2.2rem;line-height:7.68vw;line-height:1.8rem;text-align:center;color:#fff;font-style:normal;background:url(https://yx-web-nosdn.netease.im/quickhtml%2Fassets%2Fyunxin%2Fdefault%2Frecord-time.png) no-repeat;-webkit-transform:translateX(3rem);transform:translateX(3rem)
}
.m-chat-editor-main .u-editor-input .u-record-time.with-close-btn:after{content:" ";position:absolute;top:0;right:-8.533vw;right:-2rem;display:block;width:8.533vw;width:2rem;height:8.533vw;height:2rem;background:url(https://yx-web-nosdn.netease.im/quickhtml%2Fassets%2Fyunxin%2Fdefault%2Fstop-record.png) no-repeat
}
.m-chat-editor-main .u-editor-icons{position:absolute;display:inline-block;right:0;top:0;width:42.667vw;width:10rem;height:17.067vw;height:4rem
}
.m-chat-editor-main .u-editor-icons .u-editor-icon{position:relative;display:inline-block;top:calc(2rem - .8);width:6.827vw;width:1.6rem;height:6.827vw;height:1.6rem;margin-left:.2rem;vertical-align:middle
}
.m-chat-editor-main .u-editor-icons .u-editor-icon input[type=file]{position:absolute;display:inline-block;left:0;top:0;width:inherit;height:inherit;opacity:0;font-size:4.267vw;font-size:1rem
}
.m-chat-editor-main .u-editor-send{position:relative;display:inline-block;top:calc(2rem - .8);margin-left:.2rem;width:11.093vw;width:2.6rem;height:6.827vw;height:1.6rem;line-height:6.827vw;line-height:1.6rem;padding:.1rem .2rem;border-radius:.2rem;font-size:3.84vw;font-size:.9rem;color:#fff;background-color:#0091e4;text-align:center
}
.u-msg{position:relative;padding:.2rem 0
}
.u-msg.session-chat{padding:.6rem 0;clear:both
}
.u-msg .msg-head{top:.427vw;top:.1rem;margin:0 .6rem;padding:0;width:8.533vw;width:2rem;height:8.533vw;height:2rem;vertical-align:top
}
.u-msg .msg-head,.u-msg .msg-head img{position:relative;display:inline-block
}
.u-msg .msg-head img{margin:0;width:inherit;height:inherit
}
.u-msg .msg-user{font-style:italic;color:#999
}
.u-msg .msg-user em{font-weight:400;margin-right:1rem
}
.u-msg .msg-audio img{float:right;margin-right:.3rem!important
}
.u-msg .msg-text{max-width:54%;min-height:5.12vw;min-height:1.2rem;word-break:break-word;height:auto;line-height:5.12vw;line-height:1.2rem;font-size:3.84vw;font-size:.9rem;border:none
}
.u-msg .msg-text,.u-msg .msg-text img{position:relative;display:inline-block
}
.u-msg .msg-text img{margin:0;padding:0;max-width:100%;vertical-align:bottom
}
.u-msg .msg-text img.emoji-small{width:5.973vw;width:1.4rem;height:5.973vw;height:1.4rem;vertical-align:bottom
}
.u-msg .msg-text img.emoji-big{width:12.8vw;width:3rem;height:12.8vw;height:3rem;width:20.48vw;width:4.8rem;height:20.48vw;height:4.8rem;vertical-align:middle
}
.u-msg .msg-text embed,.u-msg .msg-text video{position:relative;display:inline-block;max-width:100%;max-height:34.133vw;max-height:8rem;vertical-align:bottom;background-color:#000;text-align:center;color:#fff
}
.u-msg .msg-text:after,.u-msg .msg-text:before{content:" ";position:absolute;top:3.413vw;top:.8rem;border-top:.4rem solid transparent;border-bottom:.4rem solid transparent;width:0;height:0
}
.u-msg .msg-text.notify{max-width:100%
}
.u-msg .msg-text .button{margin:.1rem 0;padding:.1rem .2rem;border:1px solid #fff;border-radius:.2rem;background-color:#e5f4ff;color:#666
}
.u-msg .msg-failed{position:relative;float:right;margin-right:.2rem;vertical-align:top;font-size:3.925vw;font-size:.92rem;line-height:8.533vw;line-height:2rem
}
.u-msg .msg-link{display:block;position:absolute;bottom:-8.533vw;bottom:-2rem;right:0;min-width:17.067vw;min-width:4rem;min-height:5.12vw;min-height:1.2rem;word-break:normal;height:auto;line-height:5.12vw;line-height:1.2rem;font-size:4.267vw;font-size:1rem;border:none
}
.u-msg .msg-link a{color:#0091e4;text-decoration:underline;float:right
}
.u-msg.session-chat .msg-text{padding:.4rem;margin-bottom:.8rem;border-radius:.4rem
}
.u-msg.session-chat .msg-robot{min-width:12.8vw;min-width:3rem;margin-bottom:3rem
}
.u-msg.session-chat.item-you .msg-head,.u-msg.session-chat.item-you .msg-text{float:left
}
.u-msg.session-chat.item-you .msg-text{background-color:#5cacde;color:#fff
}
.u-msg.session-chat.item-you .msg-text:before{right:99%;border-right:.4rem solid #5cacde
}
.u-msg.session-chat.item-you .msg-text:after{background:none
}
.u-msg.session-chat.item-me .msg-head,.u-msg.session-chat.item-me .msg-text{float:right
}
.u-msg.session-chat.item-me .msg-text{color:#666;background-color:#e5f4ff
}
.u-msg.session-chat.item-me .msg-text:before{background:none
}
.u-msg.session-chat.item-me .msg-text:after{left:99%;border-left:.4rem solid #e5f4ff
}
.u-msg.item-time{color:#ccc
}
.u-msg.item-time,.u-msg.item-tip{font-size:3.413vw;font-size:.8rem;text-align:center
}
.u-msg.item-tip .tip{margin:0 auto;width:42.667vw;width:10rem;padding:.1rem;background-color:#e0e0e0;border-radius:.4rem
}
.u-msg .notification{font-size:3.413vw;font-size:.8rem;text-align:center;color:#ccc
}
.u-msg .notify{color:#f50;font-size:3.413vw;font-size:.8rem
}


